<template>
  <div id="reply">
    <div class="title">
      <span>收到</span>
      <span>回复</span>
      <a-button type="danger" style="visibility:hidden;">
        <a-icon type="minus"/>删除
      </a-button>
    </div>
    <div class="wrapper">
      <div class="item" v-for="(item ,index) in list" :key="index">
        <a-input style="margin-right:30px;" placeholder="在此编辑收到内容" v-model="item.receive" autosize/>
        <a-input
          style="margin-right:30px;"
          placeholder="在此编辑回复内容"
          v-model="item.responce"
          autosize
        />
        <a-button type="danger" @click="remove(index)">
          <a-icon type="minus"/>删除
        </a-button>
      </div>
      <div style="display:flex;">
        <a-button class="add" @click="add">
          <a-icon type="plus"/>添加
        </a-button>
        <a-button class="add" @click="save" type="primary">
          <a-icon type="save"/>保存
        </a-button>
      </div>
      <div></div>
    </div>
  </div>
</template>
<script>
export default {
  name: "reply",
  data() {
    return {
      list: [
        {
          receive: "苹果",
          responce: "橘子"
        },
        {
          receive: "西柚",
          responce: "樱桃"
        }
      ],
      loading: ""
    };
  },
  methods: {
    add: function() {
      this.list.push({
        receive: "",
        responce: ""
      });
    },
    remove: function(index) {
      this.list.splice(index, 1);
    },
    save: function() {
      let self = this;
      self.loading = this.$message.loading("正在保存中……", 0);
      setTimeout(function() {
        self.loading;
        self.$message.success("保存成功~", 1);
      }, 1000);
    }
  }
};
</script>
<style>
.title {
  margin: 10px;
  display: flex;
}
.title span {
  flex-grow: 1;
}
.wrapper {
  display: flex;
  align-items: stretch;
  flex-direction: column;
}
.item {
  display: flex;
  flex-direction: row;
  margin: 10px;
}
.add {
  margin: 10px;
  width: 100px;
}
</style>